.notification {
  color: green;
  border: 2px solid green;
  border-radius: 5px;
  font-size: 1.5em;
  padding: 5px;
  margin: 1em auto;
  background-color: lightgray;
}

.error {
  color: red;
  border: 2px solid red;
  border-radius: 5px;
  font-size: 1.5em;
  padding: 5px;
  margin: 1em auto;
  background-color: lightgray;
}